.main-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.main-content-wrapper {
  position: relative;
  z-index: 2;

  & > .crumina-flying-balls:first-child {
    padding-top: 100px;
  }
}


/*------------- #Blocks with arrow --------------*/

.block--with-arrow-left {
  position: relative;

  &:after {
    content: '';
    display: block;
    background: url("../img/arrow-left-w-50.png") no-repeat;
    position: absolute;
    transition: all .3s ease;
    right: 100%;
    top: 50%;
    transform: translate(0, -50%);
    width: 50px;
    height: 160px;
  }
}

.block--with-arrow-bottom-center {
  position: relative;

  &:after {
	content: '';
	display: block;
	background: url("../img/arrow-bottom-center-w-150.png") no-repeat;
	position: absolute;
	transition: all .3s ease;
	left: 50%;
	top: 100%;
	transform: translate(-50%, 0);
	width: 150px;
	height: 50px;
  }
}

/*------------- Styles for Demonstration Pages --------------*/


.demonstration-color {
  display: inline-block;
  height: 200px;
  width: 200px;
  max-width: 100%;
  border-radius: 30px;
  position: relative;
}

.demonstration-color-text {
  position: absolute;
  bottom: 30px;
  left: 30px;
  color: #fff;
  font-size: 18px;
  font-weight: bold
}

