.presentation-radio-buttons {
  border-radius: 10px;
  overflow: hidden;
  padding: 40px 0;
  background-color: #3d434f;
}

.presentation-buttons {
  display: flex;
  align-items: flex-end;
}

.v-align-bottom {
  vertical-align: bottom;
  display: table-cell;
  float: none;
}

.d-flex-align-middle {
  align-items: center;
  display: flex;
}

.swal2-container.swal2-shown {
  z-index: 9999999;
}

/*------------- Responsive Mode --------------*/


@media (max-width: 1024px) {
  .block--with-arrow-left::after {
    right: 50%;
    top: 0;
    transform: translate(50%, -65%) rotate(90deg);
  }
}

@media (max-width: 800px) {
  .pt-mobile-80 {
    padding-top: 80px;
  }
}

@media (max-width: 768px) {
  .inline-items-wrap {
    flex-direction: column;
    align-items: flex-start;

    .btn + .btn {
      margin-left: 0;
      margin-right: 30px;
    }

    & > * {
      & + * {
        margin-left: 0;
        margin-top: 20px;
      }
    }
  }

  .height-860 {
    height: 300px;
  }
}